import { Component, OnInit, ViewChild } from '@angular/core';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';
import { RequirementService } from '../../_services/requirement.service';
import { AlertifyService } from '../../_services/alertify.service';
import { ShortfallDetailList } from '../../_models/ShortfallDetailList';

@Component({
  selector: 'app-short-fall-details',
  templateUrl: './short-fall-details.component.html',
  styleUrls: ['./short-fall-details.component.css']
})
export class ShortFallDetailsComponent implements OnInit {
  roleid: any =  Number(String(localStorage.getItem('RoleID')));
  items: ShortfallDetailList[];
  @ViewChild(DataTableDirective) dtElement: DataTableDirective;
  dtOptions: DataTables.Settings = {
    pagingType: 'full_numbers',
    pageLength: 25,
    retrieve: true,
    order: [[0, 'desc']]
};
  dtInstance: DataTables.Api;
  isDtInitialized: boolean = false;
  dtTrigger: Subject<ShortfallDetailList> = new Subject();

  constructor(private requirementservice: RequirementService, private alertify: AlertifyService) { }

  ngOnInit(): void {
    this.loadShortFalldetails();
  }
  loadShortFalldetails() {
    return this.requirementservice.getShortFalldetails().subscribe((items: ShortfallDetailList[]) => {
      this.items = items;
      console.log(this.items);
      if (this.isDtInitialized) {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
          dtInstance.destroy();
          this.dtTrigger.next();
        });
      } else {
        this.isDtInitialized = true;
        this.dtTrigger.next();
      }
    }, error => {
      this.alertify.error(error);
    });

  }

}
